<template>
    <div class="about">
        <img src="../../assets/us.png" class="img"/>
        <div class="navigation">
            <img src="../../assets/home.png" class="home"/>
            <div class="m-l-7" @click="navigator('/')">首页</div>
            <div class="m-l-7"> > </div>
            <div class="m-l-7">联系我们</div>
        </div>
        <div class="news">
            <div class="company-news">联系我们</div>
            <div class="company-en">CONTACT US</div>
            <div class="card">
                <div class="concat">
                    <div class="company-name">{{ info.company_name }}</div>
                    <div class="company-name-en">Shandong Shunzhi Information Technology Co., Ltd</div>
                    <div class="rows" style="margin-top: 34px;">
                        <img src="../../assets/about/icon3.png" class="icons"/>
                        电话： {{ info.contact.telephone || info.contact.phone }}
                    </div>
                    <div class="rows" style="margin-top: 23px;">
                        <img src="../../assets/about/icon2.png" class="icons"/>
                        邮箱：{{ info.contact.email }}
                    </div>
                    <div class="rows" style="margin-top: 23px;">
                        <img src="../../assets/about/icon1.png" class="icons"/>
                        地址：{{ info.contact.address }}
                    </div>
                </div>
                <div class="map" ref="mapContainer"></div>
            </div>
            <div class="concat-i">
                <div class="c-title">在线留言</div>
                <div class="c-title-en">ONLINE MESSAGE</div>
                <div class="us">
                    <div class="inputs">
                        <span style="color: #666666;">*</span>
                        <input v-model="message.name" placeholder="请填写您的称呼" class="input"/>
                    </div>
                    <div class="inputs">
                        <span style="color: #666666;">*</span>
                        <input v-model="message.phone" placeholder="请填写您的联系方式" class="input"/>
                    </div>
                    <div class="inputs">
                        <input v-model="message.company" placeholder="请填写单位名称" class="input"/>
                    </div>
                </div>
                <textarea placeholder="请填写您的需求" v-model="message.describe" class="textarea"></textarea>
                <button class="btn" @click="submit">提交</button>
            </div>
        </div>
    </div>
</template>
<script setup>
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
import { ref,onMounted, computed, reactive } from 'vue';

    const store = useStore();
    const router = useRouter();
    const info = computed(() => store.state.info);
    const cloud = store.state.cloud;
    let mapContainer = ref(null)
    let message = reactive({
        name: '',
        phone: '',
        company: '',
        describe: ''
    })

    const TMap = window.TMap

    const navigator = () => {
        router.push("/")
    }
    onMounted(() => {
        let longitude = 116.397590;
        let latitude = 39.908776;
        if (info.value.contact?.longitude) {
            longitude = info.value.contact.longitude;
            latitude = info.value.contact.latitude;
        }
        /* eslint-disable */
        const center = new TMap.LatLng(latitude, longitude)
        const map = new TMap.Map(mapContainer.value, {
            center: center, // 设置地图中心点坐标
            zoom: 15, // 设置地图缩放级别
            viewMode: '2D',
        })
        new TMap.MultiMarker({
            map: map,
            geometries: [{ position: center }],
        })
    })

    const submit = () => {
        if(!message.name) {
            alert('请输入您的称呼')
            return;
        }
        if(!message.phone) {
            alert('请输入您的联系方式')
            return;
        }

    }
</script>
